import React, { Suspense } from "react";
import { isSocialWall } from "../../../../actions/api";
import "./scss/MediaIcon.scss"
const DefaultNetwork = React.lazy(() => import("../Network/DefaultNetwork"));
const MediaIcon = (props) => {
       const { Post, IconClass, isCenter, hideVideo, show } = props;
       const isMediaType = ([3,5].includes(Post.type)) ? true : false
       const isYoutubePost = (Post.network.id === 7) ? true : false
       const isMultiImages = (Post.mediaList && Post.mediaList.length > 0) ? true : false
       const isAudio = Post.isAudio === 1 ? true : false
       const isShopIcon = Post.isProduct ? true : false;
       
       const youtube = <Suspense fallback={<></>}><DefaultNetwork network={{ "id": 7, "name": "YouTube", "icon": "youtube" }} networkClass={'tb_youtube_ico'} /></Suspense>
       const multipleMedia = <div className={`tb_multiple_ico tb__icon tb-multiple`}>  </div>
       const audioIcon = <div className="tb_audio_ico tb__icon tb-audio">  </div>
       const videoIcon = <div className={`tb__icon tb-${isSocialWall?'reel-fill tb_reel_ico':'video tb_video_ico'}`}>  </div>
       const shoppableIcon = <div className="tb_shop_ico tb__icon tb-bag">  </div>
       const roundedVideoIcon = <div className="tb_center_video_ico tb__icon tb-play-circle">  </div>

       return show ? <>{isCenter && (isYoutubePost || isMediaType) ? 
              <div className={`${IconClass} tb__media_ico_c`}>
                     {isMediaType && !isYoutubePost ? roundedVideoIcon : ''}
                     {isMediaType && isYoutubePost ? youtube : ''}
              </div>
              :null}
              
              {isShopIcon || isMultiImages || isMediaType ?
              <div className={`${IconClass} tb__media_ico_`}>
                     {isShopIcon ? shoppableIcon : ''}
                     {isMultiImages ? multipleMedia : ''}
                     {!isCenter && isMediaType && !isYoutubePost && !hideVideo ? isAudio ? audioIcon : videoIcon : ''}
              </div>: null}
              </> : null
}
export default MediaIcon