import React from "react";
import Image from '../../Elements/Media/Image';
import Video from '../../Elements/Media/Video';
import MediaIcon from '../../Elements/MediaIcon/MediaIcon';
const Media = ({ Post, CardStyle, ThemeStyle, isVisible, index, inView }) => {

  const isVideo = [3,5].includes(Post.type) && isVisible && (ThemeStyle.autoPlay == 1) ? true : false
  const size = 178
  return <div className="tb_rt_media_wrap">
    <MediaIcon Post={Post} IconClass="tb_rt_media_icon" show={true} hideVideo={true} />
    {isVideo ?
      <Video VideoClass="tb_rt_video" Post={Post} size={size} isCover={true} muted={true} autoPlay={true} observer={false} index={index} inView={inView} />
      :
      <Image ImageClass="tb_rt_image" Post={Post} size={size} observer={false} index={index} inView={inView} />
    }
  </div>
}
export default Media