import React, { Component, memo, Suspense } from "react";
import { connect } from "react-redux";
import { Swiper, SwiperSlide } from "swiper/react";
import { Autoplay, FreeMode, Mousewheel } from 'swiper/modules';
import '../scss/ThemesSwiper.scss';
import './scss/ReelTheme.scss';
import Card from "./Elements/Card"; 
import { isTagShop, isTagbox } from '../../../actions/api';
import { detectMobile, getColumns } from "../../../utils";
import { nextData } from "../../../actions/moredataAction";

class ReelTheme extends Component {
  constructor(props) {
    super(props);
    this.inputRefs = {};

    this.state = {
      loadData : false 
    }
  }

  requestData = (swiper) => {
    const { appendData, ThemeInfo, ThemeStyle, preRender, hasMoreData, nextData, loaderData } = this.props;
    if (swiper.progress > 0.8 && hasMoreData && !loaderData.isShowMoreLoading) { 
      nextData(
        isTagShop ? ThemeInfo?.webId : ThemeInfo?.wallId,
        Math.floor(Date.now() / 1000),
        ThemeStyle?.totalPosts,
        appendData.networkID,
        appendData.after,
        preRender,
        appendData.heightEvent
      );
    }
  }

  componentDidMount() { 
    setTimeout(() => {
      const classNameRole = document.querySelectorAll('.splide__slide');
      classNameRole && classNameRole.forEach((element) => {
        element.removeAttribute("aria-hidden");
        element.removeAttribute("role");
      });
    }, 100);

    const { postData } = this.props;
    if (postData && postData.length === 1) {
      const arrowLeft = document.querySelector(".tb_rt_arrow_left__");
      const arrowRight = document.querySelector(".tb_rt_arrow_right__");
      if (arrowLeft && arrowRight) {
        arrowLeft.style.display = 'none';
        arrowRight.style.display = 'none';
      }
    }
  }

  componentDidUpdate() {
    const value = Object.values(this.inputRefs)[0];
    const { postData, completeDataObject, onClickPopUpSlider, parentID } = this.props;
    if (value && Object.values(value)[5]?.length > 0) {
      const data = Object.values(value)[5];
      data.forEach((item) => {
        if (item.classList) {
          const filterID = String(item.classList.value).split(" ").find((fCheck) => String(fCheck).includes("tb_"));
          const getID = filterID?.replace("tb_", "");
          if (getID) {
            const findIndex = postData.findIndex((find) => find === getID);
            const clickD = document.querySelectorAll(`.tb_rt_post_container #${item.id}`);
            clickD.forEach((clickDD) => {
              const p_id = clickDD.getAttribute('p_id');
              if (String(parentID) === String(p_id)) {
                clickDD.addEventListener('click', (event) => {
                  event.preventDefault();
                  const itemData = completeDataObject[getID];
                  onClickPopUpSlider(findIndex, itemData);
                });
              }
            });
          }
        }
      });
    }

     this.props.loadBranding()
  }

  updatepost = (e)=>  this.setState({ loadData : true},()=> setTimeout(() => this.props.loadMoredata(), (e*1000)))
      
  render() {
    const { postData, completeDataObject, clickToShowPopUp, hasMoreData, parentID, keyRender, onClickPopUpSlider, CardStyle, ThemeStyle, ProductSetting, ThemeInfo, containerSize } = this.props;
    const inputName = parentID;
    const desktopColumn = ThemeStyle.column.desktop;
    const mobileColumn = ThemeStyle.column.mobile;
    const slidePost = ThemeStyle.slide.autoSlide;
    const slideDuration = ThemeStyle.slide.slideDuration;
    const { loadData } = this.state
    const minCardSize =  containerSize.width > 767 ? 260 : 280;
    const halfSlide = isTagbox && [151513].includes(ThemeInfo?.ownerId) ? 0 : 0.4;
    const responsiveColumns = Math.max((containerSize.width / minCardSize).toFixed(), 1)
    const columns =  getColumns(containerSize.width, ThemeStyle.column, responsiveColumns);

    return (
      <>
        {parentID && (
          <div className="tb_rt_post_container tb_swiper" ref={this.sliderHeight}>
            <Swiper
              observer={true}
              observeParents={true}
              spaceBetween={0} 
              key={keyRender}
              ref={(parentID) => (this.inputRefs[inputName] = parentID)}
              style={{ width: '100%' }}
              onScroll={(swiper) => { this.requestData(swiper); if(!loadData){ this.updatepost(3)}}}
              onTouchMove={(swiper) => { this.requestData(swiper); if(!loadData){ this.updatepost(0) }}}
              onAutoplay={(swiper) => { this.requestData(swiper); if(!loadData){ this.updatepost( detectMobile() && mobileColumn && slideDuration<5 ? 0 : 5) }}}
              speed={700} // Adjust speed for slower animation
              edgeSwipeDetection={true}
              slidesPerView={columns + halfSlide}
              touchRatio={.5} // Reduce touch ratio for slower dragging
              cssMode={detectMobile()}
              passiveListeners={true}
              freeMode={{ enabled: true, sticky: false, momentumBounceRatio: 1, momentumVelocityRatio: 1 }} // Adjust momentum for slower dragging
              autoplay={isTagShop || isTagbox ? { delay: slidePost === 1 ? slideDuration * 1000 : 99000, disableOnInteraction: false, pauseOnMouseEnter: true } : false}
              loop={!hasMoreData}
              mousewheel={{ forceToAxis: true, sensitivity: 1 }} // Reduce sensitivity for slower mousewheel scrolling
              watchSlidesProgress={true}
              modules={[Autoplay, Mousewheel, FreeMode]}
              className="tb_rt_post_slider"
            >
              {postData?.length > 0 &&
                postData.map((item, index) => {
                  const cardData = completeDataObject[item];
                  return (isTagShop || cardData.type !== 1) ? (
                    <SwiperSlide 
                        data-id={cardData.id} 
                        key={`${cardData.id}-${index}`}  // Use a combination of cardData.id and index for uniqueness
                        p_id={parentID} className={`tb_${cardData.id}`}>
                        {({ isVisible }) => (
                          <Card  key={index} isVisible={isVisible} Post={cardData} index={index} CardStyle={CardStyle} ThemeStyle={ThemeStyle} ProductSetting={ProductSetting} ThemeInfo={ThemeInfo} clickToShowPopUp={clickToShowPopUp} onClickPopUpSlider={onClickPopUpSlider} />
                        )}
                    </SwiperSlide>
                  ) : null;
                })}
            </Swiper>
          </div>
        )}
      </>
    );
  }
}



const mapStateToProps = (state) => ({
  loaderData: state.loaderData,
});

const mapDispatchToProps = (dispatch) => ({
  nextData: (wallID, timeStamp, postCount, networkId, after, postData, heightEvent, updateInStateNow) =>
    dispatch(nextData(wallID, timeStamp, postCount, networkId, after, postData, heightEvent, updateInStateNow)),
});

export default memo(connect(mapStateToProps, mapDispatchToProps)(ReelTheme));